
'use server';
/**
 * @fileOverview Generates descriptions for volunteer roles.
 *
 * - generateRoleDescriptions - A function that generates descriptions for a list of role names.
 * - GenerateRoleDescriptionsInput - The input type for the function.
 * - GenerateRoleDescriptionsOutput - The return type for the function.
 */

import { ai } from '@/ai/genkit';
import { z } from 'zod';

const GenerateRoleDescriptionsInputSchema = z.object({
  roles: z.array(z.string()).describe('A list of volunteer role names.'),
});
export type GenerateRoleDescriptionsInput = z.infer<typeof GenerateRoleDescriptionsInputSchema>;

const RoleDescriptionSchema = z.object({
    roleName: z.string().describe('The name of the role.'),
    description: z.string().describe('A brief, friendly, one-sentence description of the role.'),
});

const GenerateRoleDescriptionsOutputSchema = z.object({
  descriptions: z.array(RoleDescriptionSchema),
});
export type GenerateRoleDescriptionsOutput = z.infer<typeof GenerateRoleDescriptionsOutputSchema>;

export async function generateRoleDescriptions(input: GenerateRoleDescriptionsInput): Promise<GenerateRoleDescriptionsOutput> {
  const prompt = ai.definePrompt({
    name: 'generateRoleDescriptionsPrompt',
    input: { schema: GenerateRoleDescriptionsInputSchema },
    output: { schema: GenerateRoleDescriptionsOutputSchema },
    prompt: `You are an assistant for a church volunteer scheduling system. Your task is to generate a brief, friendly, one-sentence description for each of the following volunteer roles. Make them sound appealing and simple.

Roles:
{{#each roles}}
- {{{this}}}
{{/each}}
`,
  });

  const { output } = await prompt(input);
  return output!;
}
